
/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    .c-about-section {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-top: 1vw;
    }

    .c-about-section-top {
        display: block;
        opacity: 1;
        width: 60vw;
        color: #2f2f2f;
        position: relative;
        top: 0vh;
        overflow: initial;
    }

    .c-about-section-bottom {
        overflow: hidden;
        position: relative;
        width: 60vw;
        margin-right: 0vw;
        padding: 4.43vw 4.16vw;
        margin-top: 1vw;
    }

    .c-about-section-text {
        border-left-width: 0;
        border-right-width: 0;
        position: relative;
        display: block;
        margin-left: auto;
        margin-right: auto;
        border-left-color: transparent;
        border-right-color: transparent;
        border-left-style: solid;
        border-right-style: solid;
        border-top: 0 solid transparent;
        border-bottom: 0 solid transparent;
        color: #2f2f2f;
    }

    .c-about-section-bottom-contact {
        overflow: initial;
        position: relative;
        width: 60vw;
        padding: 2vw 2vw;
        margin-top: 1vw;
        margin-right: 0vw;
    }
}

